.greenBoxLogin {
    width: 60%;
    border-radius: 30px;
    background-color: darkseagreen;
    margin-top: -60px;
    display: flex;
}

.myForm {
    width: 100%;
    padding: 18px;
}

.allFields {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 36px;
}

label {
    font-size: 17px;
    font-weight: bold;
}

.bars {
    height: 42px;
    border-radius: 10px;
    font-size: 16px;
    cursor: pointer;
}

.inputDiv {
    display: flex;
    align-items: center;
    margin: 10px;
}

.fieldLabels {
    width: 200px;
}

.bottom {
    display: flex;
    align-items: center;
    margin-top: 40px;
}